"use client";

import { useState } from "react";
import { format } from "date-fns";
import { useQuery } from "@tanstack/react-query";
import useStore from "@/context/store";
import useRequireAuth from "@/hooks/useRequireAuth";
import { getAgent, getOrders } from "@/api";
import { KontrAgents } from "@/types";

import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
  Popover,
  PopoverTrigger,
  PopoverContent,
} from "@/components/ui/popover";
import {
  Select,
  SelectTrigger,
  SelectValue,
  SelectContent,
  SelectGroup,
  SelectItem,
} from "@/components/ui/select";
import { Calendar } from "@/components/ui/calendar";
import { CalendarDays, Search } from "lucide-react";

import { ProfileOrderTable } from "@/components/table";
import { cn } from "@/lib/utils";

const ProfileOrdersPage: React.FC = () => {
  const auth = useStore((s) => s.auth);
  useRequireAuth();

  // Фильтры селектов
  const [kontragentName, setKontragentName] = useState<string | null>(null);
  const [orderPriceStatus, setOrderPriceStatus] = useState<string | null>(null);
  const [orderDeliveryType, setOrderDeliveryType] = useState<string | null>(null);
  const [orderType, setOrderType] = useState<string | null>(null);

  // Период дат
  const [periodStart, setPeriodStart] = useState<Date | null>(null);
  const [periodEnd, setPeriodEnd]     = useState<Date | null>(null);

  // Получаем контрагентов
  const { data: agentsData = { user_kontragents: [] } } = useQuery(
    ["contragents"],
    () => getAgent(""),
    { enabled: auth }
  );
  const contrAgents: KontrAgents[] = agentsData.user_kontragents;

  // Собираем query-параметры
  const queryParams: Record<string, any> = {};
  if (kontragentName)   queryParams.kontragentName   = kontragentName;
  if (orderPriceStatus) queryParams.paymentStatus    = orderPriceStatus;
  if (orderDeliveryType)queryParams.deliveryStatus   = orderDeliveryType;
  if (orderType)        queryParams.orderType        = orderType;
  if (periodStart)      queryParams.periodStart      = format(periodStart, "yyyy-MM-dd");
  if (periodEnd)        queryParams.periodEnd        = format(periodEnd,   "yyyy-MM-dd");

  // Запрос заказов с учётом всех фильтров
  const { data: orders = [] } = useQuery(
    ["orders", queryParams],
    () => getOrders(queryParams),
    {
      enabled: auth,
      keepPreviousData: true,
    }
  );

  return (
    <div className="pt-5 pb-10">
      <div className="pb-5 grid grid-cols-1 sm:grid-cols-2 lgl:grid-cols-4 gap-6 mb-10">
        {/* Контрагент */}
        <Select onValueChange={v => setKontragentName(v === "null" ? null : v)}>
          <SelectTrigger className="w-full h-[41px] text-[14px] leading-[25px] rounded-none border-superSilver focus:ring-cerulean">
            <SelectValue placeholder="Выберите контрагента" />
          </SelectTrigger>
          <SelectContent className="rounded-none">
            <SelectGroup>
              <SelectItem value="null">Любой</SelectItem>
              {contrAgents.map(a => (
                <SelectItem key={a.id} value={a.name}>{a.name}</SelectItem>
              ))}
            </SelectGroup>
          </SelectContent>
        </Select>

        {/* Статус оплаты */}
        <Select onValueChange={v => setOrderPriceStatus(v === "null" ? null : v)}>
          <SelectTrigger className="w-full h-[41px] text-[14px] leading-[25px] rounded-none border-superSilver focus:ring-cerulean">
            <SelectValue placeholder="Статус оплаты" />
          </SelectTrigger>
          <SelectContent className="rounded-none">
            <SelectGroup>
              <SelectItem value="null">Любой</SelectItem>
              <SelectItem value="Оплачен">Оплачен</SelectItem>
              <SelectItem value="Не оплачен">Не оплачен</SelectItem>
            </SelectGroup>
          </SelectContent>
        </Select>

        {/* Статус отгрузки */}
        <Select onValueChange={v => setOrderDeliveryType(v === "null" ? null : v)}>
          <SelectTrigger className="w-full h-[41px] text-[14px] leading-[25px] rounded-none border-superSilver focus:ring-cerulean">
            <SelectValue placeholder="Статус отгрузки" />
          </SelectTrigger>
          <SelectContent className="rounded-none">
            <SelectGroup>
              <SelectItem value="null">Любой</SelectItem>
              <SelectItem value="Отгружен">Отгружен</SelectItem>
              <SelectItem value="Не отгружен">Не отгружен</SelectItem>
            </SelectGroup>
          </SelectContent>
        </Select>

        {/* Тип заказа */}
        <Select onValueChange={v => setOrderType(v === "null" ? null : v)}>
          <SelectTrigger className="w-full h-[41px] text-[14px] leading-[25px] rounded-none border-superSilver focus:ring-cerulean">
            <SelectValue placeholder="Тип заказа" />
          </SelectTrigger>
          <SelectContent className="rounded-none">
            <SelectGroup>
              <SelectItem value="null">Любой</SelectItem>
              <SelectItem value="new">Новые</SelectItem>
              <SelectItem value="old">Старые</SelectItem>
              <SelectItem value="rejected">Отменен</SelectItem>
            </SelectGroup>
          </SelectContent>
        </Select>

        {/* Период от */}
        <Popover>
          <PopoverTrigger asChild>
            <Button
              variant="outline"
              className={cn(
                "w-full h-[41px] bg-white text-left font-normal",
                !periodStart && "text-muted-foreground"
              )}
            >
              {periodStart ? format(periodStart, "dd.MM.yyyy") : "Период от"}
              <CalendarDays className="ml-auto h-4 w-4" />
            </Button>
          </PopoverTrigger>
          <PopoverContent className="w-full p-0">
            <Calendar
              mode="single"
              selected={periodStart}
              onSelect={date => setPeriodStart(date)}
              initialFocus
            />
          </PopoverContent>
        </Popover>

        {/* Период до */}
        <Popover>
          <PopoverTrigger asChild>
            <Button
              variant="outline"
              className={cn(
                "w-full h-[41px] bg-white text-left font-normal",
                !periodEnd && "text-muted-foreground"
              )}
            >
              {periodEnd ? format(periodEnd, "dd.MM.yyyy") : "Период до"}
              <CalendarDays className="ml-auto h-4 w-4" />
            </Button>
          </PopoverTrigger>
          <PopoverContent className="w-full p-0">
            <Calendar
              mode="single"
              selected={periodEnd}
              onSelect={date => setPeriodEnd(date)}
              initialFocus
            />
          </PopoverContent>
        </Popover>

        {/* Поиск текста */}
        <div className="relative w-full">
          <Input
            type="text"
            placeholder="Заказ, серийный номер, название"
            className="h-[41px] text-[14px] leading-[25px] pr-10"
          />
          <Search className="absolute right-3 top-1/2 -translate-y-1/2" size={20} />
        </div>
      </div>

      {/* Таблица с результатами */}
      <div className="overflow-x-auto" style={{ scrollbarWidth: "none", msOverflowStyle: "none" }}>
        <ProfileOrderTable orders={orders} />
      </div>
    </div>
  );
};

export default ProfileOrdersPage;
